import Head from 'next/head'
import { Menu } from 'antd'
import { Carousel, Card, Row, Col, Rate } from 'antd'

export default () => (
  <div>
    <Head>
      <link rel='stylesheet' href='//cdnjs.cloudflare.com/ajax/libs/antd/2.9.3/antd.min.css' />
    </Head>
    <div className="header">
    <Menu mode="horizontal" theme="dark">
      <Menu.Item key="product">
        <h3>Product</h3>
      </Menu.Item>
      <Menu.Item key="About">
        <h3>About</h3>
      </Menu.Item>
    </Menu>
    </div>
    <Carousel autoplay>
      <div><h3>1</h3></div>
      <div><h3>2</h3></div>
      <div><h3>3</h3></div>
      <div><h3>4</h3></div>
    </Carousel>
    <div className="header">Header</div>
    <div className="content">Content</div>

    <Row gutter={16}>
      <Col span={6}>
          1
      </Col>
      <Col span={6}>
          2
      </Col>
      <Col span={6}>
          3
      </Col>
      <Col span={6}>
          4
      </Col>
    </Row>


    <style jsx global>{`
  .ant-carousel .slick-slide {
    text-align: center;
    height: 160px;
    line-height: 160px;
    background: #367279;
    overflow: hidden;
  }
      
  .ant-carousel .slick-slide h3 {
    color: #fff;
  }
`}</style>

  </div>
)